/* App.css */

.container {
  max-width: 600px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
  padding: 20px;
}

.title {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  font-family: "Comic Sans MS", cursive, sans-serif;
}

.subtitle {
  text-align: center;
  font-size: 18px;
  margin-bottom: 20px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form-row {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

label {
  width: 150px;
  font-weight: bold;
  font-size: 14px;
}

input {
  flex: 1;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.submit-button {
  background-color: black;
  color: white;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

.submit-button:hover {
  background-color: #333;
}

.output {
  margin-top: 30px;
}

.output h3 {
  font-size: 18px;
  font-weight: bold;
}

.output p {
  font-size: 14px;
  margin: 5px 0;
}

.error {
  color: red;
}
